import React from 'react'

const Persons = (props) => {
  return (
    <div>
      <h2>Numerot</h2>
      <table>
        <thead>
          {props.persons.map(person => <Person
            key={person.id}
            name={person.name}
            number={person.number}
            id={person.id}
            deleteName={props.deleteName}
          />)}
        </thead>
      </table>
    </div>
  )
}

const Person = (props) => {
  return (
    <tr>
      <td>{props.name}</td>
      <td>{props.number}</td>
      <td><button onClick={(event) => { props.deleteName(event, props.id, props.name) }}>poista</button></td>
    </tr>
  )
}

export default Persons