import React from 'react';
import axios from 'axios';
import Persons from './components/Persons';
import Form from './components/Form';

const baseUrl = '/api/persons';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            persons: props.persons,
            newName: '',
            newNumber: '',
        };
    }

    componentDidMount() {
        axios.get(baseUrl).then((response) => {
            this.setState({ persons: response.data });
        });
    }

    addName = (event) => {
        event.preventDefault();
        const result = this.state.persons.find(({ name }) => name === this.state.newName);

        if (!result) {
            const personObject = {
                name: this.state.newName,
                number: this.state.newNumber,
            };
            axios.post(baseUrl, personObject).then((response) => {
                this.setState({
                    persons: response.data,
                    newName: '',
                    newNumber: '',
                });
            });
        } else {
            alert('Name already exists!');
        }
    };

    deleteName = (event, id, name) => {
        event.preventDefault();
        if (window.confirm('Are you sure you want to delete ' + name)) {
            axios.delete(baseUrl + '/' + id).then((response) => {
                this.setState((previousState) => {
                    return {
                        persons: previousState.persons.filter((person) => person.id !== id),
                    };
                });
            });
        }
    };

    handleNameChange = (event) => {
        this.setState({ newName: event.target.value });
    };

    handleNumberChange = (event) => {
        this.setState({ newNumber: event.target.value });
    };

    render() {
        return (
            <div>
                <Form
                    newName={this.state.newName}
                    newNumber={this.state.newNumber}
                    addName={this.addName.bind(this)}
                    handleNameChange={this.handleNameChange.bind(this)}
                    handleNumberChange={this.handleNumberChange.bind(this)}
                />
                <Persons persons={this.state.persons} deleteName={this.deleteName.bind(this)} />
            </div>
        );
    }
}

export default App;
